import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-ask',
  templateUrl: './ask.component.html',
  styleUrls: ['./ask.component.css']
})
export class AskComponent implements OnInit {

  public question = 'Bonjour, veux-tu que je te raconte une blague ?';
  public bool = true;

  constructor(
    private router: Router
  ) { }

  ngOnInit() {
    QiSession(session =>
      session.service('ALMemory').then(ALMemory => {
        console.log('Raise event');
        ALMemory.raiseEvent('ask', this.question);
        console.log('Event raised');
        ALMemory.subscriber('WordRecognized').then(subscriber => {
          const id = subscriber.signal.connect(state => {
            if (state[0].match('.*oui*.') && state[1] > 0.42) {
              subscriber.signal.disconnect(id);
              if (this.bool) {
                this.bool = false;
                this.onYes();
              }
            }
            if (state[0].match('.*non*.') && state[1] > 0.42) {
              subscriber.signal.disconnect(id);
              if (this.bool) {
                this.bool = false;
                this.onNo();
              }
            }
          })
        })
      })
    , () =>  console.log('disconnected')
    );
  }

  onYes(): void {
    this.router.navigate(['joke']);
  }

  onNo(): void {
    this.router.navigate(['sad']);
  }

}



