import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-joke',
  templateUrl: './joke.component.html',
  styleUrls: ['./joke.component.css']
})
export class JokeComponent implements OnInit {

  public joke: string;

  constructor(private router: Router) { }

  ngOnInit() {
    QiSession(session =>
      session.service('ALMemory').then(ALMemory => {
        ALMemory.raiseEvent('joke', true);
        ALMemory.subscriber('getJoke').then(subscriber => {
          const id = subscriber.signal.connect(state => {
            this.joke = state;
            subscriber.signal.disconnect(id);
          })
        });
        ALMemory.subscriber('jokeSaid').then(subscriber => {
          const id = subscriber.signal.connect(state => {
            this.router.navigate(['analysis']);
            subscriber.signal.disconnect(id);
          })
        })
      })
    , () =>  console.log('disconnected')
    );
  }

}



