import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AnalysisComponent } from './analysis/analysis.component';
import { AskComponent } from './ask/ask.component';
import { JokeComponent } from './joke/joke.component';
import { LaughtComponent } from './laught/laught.component';
import { ReaskComponent } from './reask/reask.component';
import { SadComponent } from './sad/sad.component';

const appRoutes: Routes = [
  { path: '', component: AskComponent },
  { path: 'joke', component: JokeComponent },
  { path: 'laught', component: LaughtComponent },
  { path: 'reask', component: ReaskComponent },
  { path: 'sad', component: SadComponent },
  { path: 'analysis', component: SadComponent }
];
@NgModule({
  imports: [
    RouterModule.forRoot(appRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class AppRoutingModule {}



